
import { defineComponent } from "vue";
import { NotificationService } from "@/services/NotificationService";
import { AdminService } from "@/services/AdminService";
import { User } from "@/model/User";

export default defineComponent({
  data() {
    const users: (User & { uid: string })[] = [];
    const roles: { [role: string]: string[] } = {};
    const selectedUsers: string[] = [];
    //const selectedRoles: string[] = [];
    const selectedRole = "";
    return {
      topic: "",
      users,
      roles,
      selectedUsers,
      // selectedRoles,
      selectedRole,
    };
  },
  async mounted() {
    const adminService = new AdminService();
    this.users = await adminService.getAllUsers();
    this.roles = await adminService.getAllRoles();
  },
  methods: {
    async createTopic() {
      const tokens = this.users
        .filter((u) => this.selectedUsers.includes(u.uid))
        .flatMap((u) =>
          Object.keys(u.notificationTokens ? u.notificationTokens : {})
        );
      console.log(tokens);
      if (this.topic !== "" && tokens.length > 0) {
        await new NotificationService().addToTopic(tokens, this.topic);
        await new AdminService().saveTopic(this.topic, tokens);
        this.$router.back();
      }
    },
    toggleFromList(el: string, list: string[]) {
      const i = list.indexOf(el);
      if (i >= 0) {
        list.splice(i, 1);
      } else {
        list.push(el);
      }
    },
    toggleRole(role: string) {
      // this.toggleFromList(role, this.selectedRoles);
      // this.roles[role].forEach((r) =>
      //   this.toggleFromList(r, this.selectedUsers)
      // );
      if (this.selectedRole === role) {
        this.selectedRole = "";
        this.selectedUsers = [];
      } else {
        this.selectedRole = role;
        this.selectedUsers = this.roles[role];
      }
    },
  },
});
