import { User } from "@/model/User";
import { FirebaseDBService } from "./FirebaseDBService"

export class AdminService {

    private db: FirebaseDBService;
    // private store: Store<RootState>;

    constructor() {
        this.db = new FirebaseDBService();
        // this.store = store;
    }

    async getAllUsers() {
        const res = await this.db.readData<{ [uid: string]: User }>('users');
        const users = [];
        if (res) {
            for (const uid in res) {
                const user = { ...res[uid], uid };
                users.push(user);
            }
        }
        return users;
    }

    async getAllTopics() {
        const res = await this.db.readData<{ [topic: string]: true }>('notifications/topics');
        if (res) {
            return Object.keys(res);
        }
        return [];
    }

    async getAllRoles(): Promise<{ [role: string]: string[]; }> {
        const roles = await this.db.readData<{ [role: string]: Permission }>('permissions');
        const formattedRoles: { [role: string]: string[] } = {};
        if (roles) {
            for (const role in roles) {
                formattedRoles[role] = Object.keys(roles[role]);
            }
        }
        return formattedRoles;
    }

    getTokensFromUsers(users: User[]) {
        return users.flatMap((user) => {
            if (user.notificationTokens) {
                return Object.keys(user.notificationTokens);
            }
        });
    }

    async saveTopic(topic: string, tokens: string[]) {
        const topicObj: { [topic: string]: true } = tokens.reduce(
            (tObj: { [topic: string]: true }, t) => ({ ...tObj, [t]: true }), {}
        );
        this.db.writeData<{ [topic: string]: true }>(`notifications/topics/${topic}`, topicObj);
    }

}

export type Permission = {
    [uid: string]: true;
}
