import { Message } from "@/model/FcmMessage";

export class NotificationService {

    async sendNotification(message: Message) {
        const myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");

        const raw = JSON.stringify(message);

        const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
        };

        const res = await fetch("https://unruffled-liskov-633292.netlify.app/.netlify/functions/send", requestOptions);
        return await res.json();
        
    }
    
    async addToTopic(tokens: string[], topic: string) {
        const myHeaders = new Headers();
        myHeaders.append("Content-Type", "application/json");
        
        const raw = JSON.stringify({tokens, topic});
        
        const requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
        };
        
        const res = await fetch("https://unruffled-liskov-633292.netlify.app/.netlify/functions/addToTopic", requestOptions);
        return await res.json();
    }
    
    async getTopics() {
        //
    }
}